<template>
    <div class="slide-show" @mouseover="clearInv()" @mouseout="runInv">
      <div class="slide-img">
        <a :href="slides[nowIndex].href">
          <transition name="slide-trans">
            <img v-if="isShow" :src="slides[nowIndex].src" >
          </transition>
          <transition name="slide-trans-old">
            <img v-if="!isShow" :src="slides[nowIndex].src" >
          </transition>
        </a>
      </div>

      <h2>{{slides[nowIndex].title}}</h2>
      <ul class="slide-pages">
        <li @click="goto(prevIndex)"> < </li>
        <li v-for="(item ,index) in slides" @click="goto(index)">
          <a :class="{on: index===nowIndex}">{{index+1}}</a>
        </li>
        <li @click="goto(nextIndex)"> > </li>
      </ul>
    </div>
</template>

<script>
  export default {
    props:{
      slides:{
        type:Array,
        default:[]
      },
      inv:{
        type:Number,
        default:2000
      }
    },
    data(){
      return{
        nowIndex:0,
        isShow: false
      }
    },
    computed:{
      prevIndex(){
        if(this.nowIndex===0){
          return this.slides.length-1
        }else{
          return this.nowIndex-1
        }
      },
      nextIndex(){
        if(this.nowIndex===this.slides.length-1){
          return 0
        }else{
          return this.nowIndex+1
        }
      },
    },
    methods:{
      goto(index) {
        this.isShow = false
        setTimeout(() => {
          this.isShow = true
          this.nowIndex = index
          this.$emit('onchange,index')
        }, 10)
      },
      clearInv(){
        clearInterval(this.invId)
      },
      runInv(){
        this.invId=setInterval(()=>{
          this.goto(this.nextIndex)
        },this.inv)
      }
    },
    mounted(){
      this.runInv()
    }
  }
</script>

<style scoped>
  .slide-trans-enter-active {
    transition: all .5s;
  }
  .slide-trans-enter {
    transform: translateX(1000px);
  }
  .slide-trans-old-leave-active {
    transition: all .5s;
    transform: translateX(-1000px);
  }
  .slide-show {
    position: relative;
    margin: 15px 15px 15px 0;
    width: 100%;
    height: 400px;
    overflow: hidden;
  }
  .slide-show h2 {
    position: absolute;
    width: 1200px;
    height: 100%;
    color: #000000;
    background: rgba(128,128,128 ,0.5);
    opacity: .5;
    bottom: 0;
    height: 30px;
    text-align: left;
    padding-left: 15px;
    padding-top: 10px;
  }
  .slide-img {
    width: 100%;
  }
  .slide-img img {
    width: 100%;
    position: absolute;
    top: 0;
  }
  .slide-pages {
    position: absolute;
    bottom: 10px;
    right: 15px;
  }
  .slide-pages li {
    display: inline-block;
    padding: 0 10px;
    cursor: pointer;
    color: #000000;
  }
  .slide-pages li .on {
    text-decoration: underline;
  }
</style>



